.footer {
  padding: 17px 0;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.contacts {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.social {
  display: flex;
  gap: 16px;
}

.link {
  color: #00C788;
  transition: color .3s ease-in-out;
}

.link:hover {
  color: #4af4a5;
}

@media (max-width: 480px) {
  .wrapper {
    flex-direction: column-reverse;
  }
}